import React from 'react';
import generateClasses from './Product.classes.js';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { IconButton } from '@material-ui/core';

const Product = ({ product, onClickAddProduct }) => {
  const classes = generateClasses();
  return (
    <Card className={classes.root}>
      <CardMedia
        className={classes.media}
        image={product.imageUrl}
        title="Contemplative Reptile"
      />
      <CardContent>
        <Typography variant="h5" align="center" component="h1">
          {product.name}
        </Typography>
        <Typography
          variant="subtitle1"
          component="p"
          align="center"
          color="textSecondary"
        >
          {product.subtitle}
        </Typography>
        <Typography variant="body1" component="p">
          {product.description}
        </Typography>
        <Typography variant="caption" component="p" color="textSecondary">
          {product.aditionalInfo}
        </Typography>
      </CardContent>
      <CardActions className={classes.cardActions}>
        <Typography variant="h6" component="p">
          {`COP ${product.price
            .toString()
            .replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1.')}`}
        </Typography>
        <IconButton onClick={onClickAddProduct}>
          <i className={`${classes.buyIcon} las la-cart-plus`}></i>
        </IconButton>
      </CardActions>
    </Card>
  );
};

export default Product;
